'use client'

interface Todo {
  userId: number
  id: number
  title: string
  completed: boolean
}

const FetchingPage = async () => {
  const url = 'https://jsonplaceholder.typicode.com/todos'
  const response = await fetch(url)
  const todos: Todo[] = await response.json()

  console.log(todos[0])

  return (
    <div>
      {todos[0].title}
    </div>
  )
}
export default FetchingPage
